import axios from '@common/plugins/Axios'

export default {
  login: ({ client_id, loginAcct, password, redirect_uri, response_type, scopes, state }) => {
    return axios.request({
      url: `/oauth/authorize`,
      method: 'post',
      params: {
        client_id, loginAcct, password, redirect_uri, response_type, scopes, state
      }
    })
  }
}
