import constants from '@common/constants'
import { lsInstance, makeTree } from '@common/util'
import config from '@/config'
export default {
  menuTree: state => {
    const principal =
      state.principal || JSON.parse(lsInstance.getItem(constants.PRINCIPAL_KEY))
    return principal
      ? principal.menus === null
        ? null
        : makeTree(
          'id',
          'parent',
          principal.menus.filter(menu => {
            return (
              menu.parent !== config.system.menuRoot
            )
          })
        )
      : {}
  },
  user: state => {
    const principal =
      state.principal || JSON.parse(lsInstance.getItem(constants.PRINCIPAL_KEY))
    if (principal === null) {
      return {}
    }
    let { userId, loginAcct, username, mobile } = principal
    return { userId, loginAcct, username, mobile }
  },
  indexPage: state => {
    const principal =
      state.principal || JSON.parse(lsInstance.getItem(constants.PRINCIPAL_KEY))
    if (principal === null) {
      return '/'
    } else {
      let urledMenus = principal.menus.filter(menu => {
        return menu.url
      })
      return urledMenus.length > 0 ? urledMenus[0].url : '/'
    }
  },
  hasPermission: state => authCode => {
    const principal =
      state.principal || JSON.parse(lsInstance.getItem(constants.PRINCIPAL_KEY))
    if (principal !== null) {
      let permissionE = principal.elements.filter(e => {
        return e.privilegeCode === authCode
      })
      return permissionE.length > 0
    }
    return false
  },
  hasRouterPermission: state => url => {
    const principal =
      state.principal || JSON.parse(lsInstance.getItem(constants.PRINCIPAL_KEY))
    if (principal === null) {
      return false
    }
    let menuFilter = principal.menus.filter(e => {
      return e.url === url
    })
    if (menuFilter.length > 0) {
      return true
    }
    return false
  },
  accessToken: state => {
    return lsInstance.getItem(constants.ACCESS_TOKEN_KEY)
  }
}
