import api from '@common/api/index'
import constants from '@common/constants'
import { toSSO } from '@common/util'
import config from '@/config'
import router from '@router'

export default {
  async initJwtToken ({ commit, dispatch, state }, { code }) {
    const tokenRes = await api.auth.getJwtToken(code)
    if (tokenRes === undefined) {
      dispatch('logout')
    }
    let { code: tokenState, data: tokenData } = tokenRes.data
    if (constants.RESP_CODE_SUCESS !== tokenState) {
      alert('接口异常')
    }
    let { jwtToken, accessToken } = tokenData
    commit('setToken', { token: jwtToken })
    commit('setAccessToken', { accessToken: accessToken })
    const principalRes = await api.auth.getUser()
    return principalRes
  },
  setPrincipal ({ commit }, principalData) {
    let { code: principalState, data: principal } = principalData.data
    if (constants.RESP_CODE_SUCESS !== principalState) {
      alert('接口异常')
    }
    commit('setPrincipal', { principal: principal })
  },
  logout ({ commit }) {
    commit('clearSession')
    toSSO()
  },
  /**
   * 单点接口
   * @param context
   * @param path 单点路径 sso:CLIENT_ID:///myMenu?name=1
   * @returns {Promise<any>}
   */
  singleSign (context, { path }) {
    let protocolRegex = /^sso:.+:\/\/\/.+/
    return new Promise((resolve, reject) => {
      if (protocolRegex.test(path)) {
        let targetUrl = path.replace(/^sso:.+:\/\//, '')
        let clientId = path.match(/^sso:(\S*):\/\//)[1]
        let param = {
          'access_token_value': context.getters['accessToken'],
          'response_type': config.authorization.responseType,
          'scope': config.authorization.scope,
          'state': config.authorization.state,
          'client_id': clientId
        }
        api.auth.singleSign(param).then(res => {
          if (res && res.data && res.data.code === '200') {
            let code = res.data.data.code
            let targetUris = res.data.data.redirectUris
            let url = targetUris[1] || targetUris[0]
            if (!url) {
              reject(new Error('目标系统未配置首页地址!'))
            }
            resolve({ code, targetUrl: `${url}${targetUrl}` })
          } else {
            reject(new Error('单点失败,请联系管理员!'))
          }
        })
      } else {
        reject(new Error('单点地址格式错误!'))
      }
    })
  },
  /**
   *
   * @param context
   * @param path 菜单路径
   * @param callback 如果是单点菜单,单点认证成功后回调
   * @returns {boolean}
   */
  linkTo (context, { path, callback }) {
    if (!path) {
      return false
    }
    if (/^sso:.*?:\/\//.test(path)) {
      context.dispatch('singleSign', { path }).then(res => {
        if (typeof callback === 'function') {
          callback(res.targetUrl, res.code)
        } else {
          window.open(`${res.targetUrl}${/\?\S+=/.test(res.targetUrl) ? '&' : '?'}code=${res.code}`)
        }
      })
      // .catch(error => this.$message(error.message))
    } else {
      router.push(path)
    }
  }
}
