<template>
  <div>
    <el-container>
      <el-main  style="height: calc(100% - 50px)">
        <el-row type="flex"  :gutter="20" justify="center">
          <el-col  :span="8"   class="login-aside"></el-col>
          <el-col :span="8" >

            <el-form style="padding-top: 20px"  ref="loginForm" :rules="loginRules"  :model="loginData" label-width="80px">
              <el-form-item style="text-align: center">
                <span style="color: #cbced5;font-size: large;">天源迪科/技术支撑中心/统一认证平台</span>
              </el-form-item>
              <el-form-item  prop="loginAcct">
                <el-input v-model="loginData.loginAcct"  placeholder="请输入用户名" @keyup.enter.native="submitForm('loginForm')">
                  <template slot="prepend">用户</template>
                </el-input>
              </el-form-item>
              <el-form-item  prop="password">
                <el-input type="password" placeholder="请输入密码" autocomplete="off" v-model="loginData.password"  @keyup.enter.native="submitForm('loginForm')">
                  <template slot="prepend">密码</template>
                </el-input>
              </el-form-item>
              <el-form-item>
                <el-button style="width: 100%" type="primary" :disabled="loginButtonDisable" @click="submitForm('loginForm')">登录</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="8" class="login-aside"></el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import aucApi from '@business/auc/api'
export default {
  name: 'login',
  props: {
    client_id: String,
    redirect_uri: String,
    response_type: String,
    state: String
  },
  data () {
    return {
      loginButtonDisable: true,
      loginData: {
        loginAcct: '',
        password: '',
        client_id: this.client_id?this.client_id:process.env.VUE_APP__PORTAL_CLIENT_ID,
        redirect_uri: this.redirect_uri?this.redirect_uri:process.env.VUE_APP__PORTAL_REDIRECT_URL,
        response_type: this.response_type?this.response_type:process.env.VUE_APP__RESPONSE_TYPE,
        scopes: '',
        state: this.state?this.state:process.env.VUE_APP__STATE
      },
      loginRules: {
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        loginAcct: [
          { required: true, message: '请输入账号', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    let parameterCheckTips = []
    if (!this.loginData.client_id) {
      parameterCheckTips.push('client_id')
    }
    if (!this.loginData.redirect_uri) {
      parameterCheckTips.push('redirect_uri')
    }
    if (!this.loginData.response_type) {
      parameterCheckTips.push('response_type')
    }

    this.loginButtonDisable = parameterCheckTips.length > 0
    if (this.loginButtonDisable) {
      this.$message({
        showClose: true,
        message: `门户未配置，请完善URL中目标系统${parameterCheckTips.join(',')}参数信息`,
        type: 'error'
      })
    }
  },
  methods: {
    submitForm (formName) {
      if (this.loginButtonDisable) {
        return
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loginData.redirect_uri = decodeURIComponent(this.loginData.redirect_uri)
          aucApi.login(
            this.loginData
          ).then(res => {
            if (res && res.data.code === '200') {
              /**
               * 重定向至应用首页
               * @type {string}
               */
              window.location.href = `${this.loginData.redirect_uri}?code=${res.data.data}`
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style scoped>
  .login-aside{
    /*background-color: #e4e5ec;*/
    border-radius: 30px;
  }
  .el-container{
    background: url("../../../assets/images/login_back.jpg") no-repeat;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
  }
  .el-main{
    padding-top: 8%;
  }
</style>
