import Vue from 'vue'
import Router from 'vue-router'
import Error from '@common/components/tips/Error'
import Login from '@business/auc/component/Login'
Vue.use(Router)
const routePaths = {
  home: '/'
}
const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: routePaths.home,
      name: 'home',
      props: route => ({
        client_id: route.query.client_id,
        redirect_uri: route.query.redirect_uri,
        response_type: route.query.response_type,
        state: route.query.state
      }),
      component: Login
    },
    {
      path: '/auth/error',
      name: 'Error',
      props: route => ({ errorCode: route.query.errorCode }),
      component: Error
    }
  ]
})

router.beforeEach((to, from, next) => {
  let errorCode = 200
  let pathExists = false
  // 判断是否合法路由
  for (let path in routePaths) {
    if (routePaths[path] === to.path) {
      pathExists = true
      break
    }
  }
  if (!pathExists) {
    errorCode = 404
  }
  errorCode === 200 ? next() : next('/auth/error?errorCode=' + errorCode)
}
)
export default router
